import React, { useState, useEffect, Fragment } from 'react';
import Waiting from './Waiting.js'
import { makeStyles } from '@material-ui/core/styles'
import * as ARBApi from '../../utils/ARBApi'
import Match from './Match'
import StatsPlayers from './StatsPlayers'
import {Grid, Paper} from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'justify',
    color: theme.palette.text.secondary,
  },
}));

export default function GridCards() {
  const classes = useStyles();
  const [lastMatch, setLastMatch] = useState([]);
  const [nextMatch, setNextMatch] = useState([]);
  const [appareances, setAppareances] = useState([]);
  const [goals, setGoals] = useState([]);

  useEffect(() => {
    ARBApi.getAll(`lastMatch`).then((lastMatch) => {
      setLastMatch(lastMatch);
    });    
    ARBApi.getAll(`nextMatch`).then((nextMatch) => {
      setNextMatch(nextMatch);
    });
    ARBApi.getAll(`appareances`).then((appareances) => {
      setAppareances(appareances);
    });    
    ARBApi.getAll(`goals`).then((goals) => {
      setGoals(goals);
    });
  }, []);

  return (
    <Fragment>
    {(lastMatch.length>0) ? (
      <Grid item xs={12} sm={3}>
        <Paper className={classes.paper}>
          <Match
            match={lastMatch[0]} 
            title='Ultima partita'
          />
        </Paper>
      </Grid>
      ):(
        <Grid item xs={12} sm={3}>
          <Paper className={classes.paper}>
            <Waiting/>
          </Paper>
        </Grid>        
        )
      }
      {(nextMatch.length>0) ? (
      <Grid item xs={12} sm={3}>
        <Paper className={classes.paper}>
          <Match
            match={nextMatch[0]} 
            title='Prossima Partita'
          />
        </Paper>
      </Grid>
      ):(
        <Grid item xs={12} sm={3}>
          <Paper className={classes.paper}>
            <Waiting/>
          </Paper>
        </Grid>        
        )
      }
      {(goals.length>0) ? (
      <Grid item xs={12} sm={3}>
        <Paper className={classes.paper}>
          <StatsPlayers
            player={goals[0]} 
            type='Gol'
          />
        </Paper>
      </Grid>
      ):(
      <Grid item xs={12} sm={3}>
        <Paper className={classes.paper}>
          <Waiting/>
        </Paper>
      </Grid>        
      )
      }
      {(appareances.length>0) ? (
      <Grid item xs={12} sm={3}>
        <Paper className={classes.paper}>
          <StatsPlayers
            player={appareances[0]} 
            type='Presenze'
          />
        </Paper>
      </Grid>
      ):(
        <Grid item xs={12} sm={3}>
          <Paper className={classes.paper}>
            <Waiting/>
          </Paper>
        </Grid>        
        )
      }
      </Fragment>
  );
}
